<template>
  <div class="about">
    <van-nav-bar>
      <template #title>
        <div class="square-title">关于漫画</div>
      </template>
      <template #left>
        <div class="right-icon">
          <img
            src="../assets/images/left.png"
            alt
            class="auto-img"
            @click="goback"
          />
        </div>
      </template>
    </van-nav-bar>
    <div class="bg-img"><img src="../assets/images/about.png" alt="" class="auto-img" /></div>
    <div class="titleH">
      <div class="title-auto clearfix">
       <div class="title-img">
          <img src="../assets/images/about1.png" alt="" class="auto-img fl">
       </div>
        <div class="title-name fr">作品状态</div>
      </div>
    </div>
    <div ref="statuschart" style="width: 100%; height: 350px"></div>
    <div class="titleH">
      <div class="title-auto clearfix">
       <div class="title-img">
          <img src="../assets/images/about2.png" alt="" class="auto-img fl">
       </div>
        <div class="title-name fr">作品类别</div>
      </div>
    </div>
    <div ref="typechart" style="width: 100%; height: 400px"></div>
    <div class="titleH">
      <div class="title-auto clearfix">
       <div class="title-img">
          <img src="../assets/images/about3.png" alt="" class="auto-img fl">
       </div>
        <div class="title-name fr">地域区分</div>
      </div>
    </div>
    <div ref="regionchart" style="width: 100%; height: 350px"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 作品类别数据
      typeData: [],
      // 作品状态数据
      statusData: [],
      // 地域区分数据
      regionData: [],
      // 图一标题
      chart1Title:["最近更新", "热门排行", "连载中", "已完结 "],
      // 图二数据
      chart2: [],
      //图二标题
      chart2Title: [
        "少年热血",
        "武侠格斗",
        "恐怖灵异",
        "耽美人生",
        "少女爱情",
        "恋爱生活",
        "生活漫画",
        "科幻魔幻",
        "竞技体育",
        "爆笑喜剧",
        "侦探推理",
      ],
      //图三数据
      chart3: [],
      //图三标题
      chart3Title: [
        "韩国漫画",
        "香港漫画",
        "大陆漫画",
        "日本漫画",
        "台湾漫画",
        "欧美漫画",
        "其他漫画",
      ],
    };
  },
  created() {
    let opus=JSON.parse(localStorage.getItem("opus"));
    this.statusData = opus[0].statusData;
    this.typeData = opus[0].typeData;
    this.regionData = opus[0].regionData;
    // 图二数据
    this.chardata(this.typeData,this.chart2Title,this.chart2);
    // 图三数据
    this.chardata(this.regionData,this.chart3Title,this.chart3);
  },
  mounted() {
    // 显示图表
    this.getEchartData();
  },
  methods: {
    //回退
    goback() {
      this.$router.back();
    },
    // 图表数据
    chardata(datas,title,chart){
      for (let i = 0; i < datas.length; i++) {
        let c2 = {
          value: datas[i],
          name:  title[i],
        };
        chart.push(c2);
      }
    },
    //绘制图表
    getEchartData() {
      const chart1 = this.$refs.statuschart;
      const chart2 = this.$refs.typechart;
      const chart3 = this.$refs.regionchart;
      if (chart1) {
        const myChart = this.$echarts.init(chart1);
        const option = {
          // 局部调色盘
          color: ["#016831"],
          tooltip: {
            trigger: "axis",
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: "shadow", // 默认为直线，可选为：'line' | 'shadow'
            },
          },
          grid: {
            left: "3%",
            right: "4%",
            bottom: "3%",
            containLabel: true,
          },
          xAxis: [
            {
              type: "category",
              data: this.chart1Title,
              axisTick: {
                alignWithLabel: true,
              },
            },
          ],
          yAxis: [
            {
              type: "value",
            },
          ],
          series: [
            {
              type: "bar",
              barWidth: "60%",
              data: this.statusData,
              label: {
                show: true,
                position: "inside",
              },
            },
          ],
        };

        myChart.setOption(option);
        window.addEventListener("resize", function () {
          myChart.resize();
        });
      }
      if (chart2) {
        const myChart = this.$echarts.init(chart2);
        const option = {
          tooltip: {
            trigger: "item",
            formatter: "{d}%",
          },
          legend: {
            bottom: 0,
            data:this.chart2Title
          },
          series: [
            {
              name: "访问来源",
              type: "pie",
              radius: "55%",
              roseType: "angle",
              data: this.chart2,
            },
          ],
        };
        myChart.setOption(option);
        window.addEventListener("resize", function () {
          myChart.resize();
        });
      }
      if (chart3) {
        const myChart = this.$echarts.init(chart3);
        const option = {
          tooltip: {
            trigger: "item",
            formatter: "{d}%",
          },
          legend: {
            bottom: 10,
            data:this.chart3Title,
          },
          series: [
            {
              name: "访问来源",
              type: "pie",
              radius: "55%",
              data:this.chart3
            },
          ],
        };
        myChart.setOption(option);
        window.addEventListener("resize", function () {
          myChart.resize();
        });
      }
    },
  },
};
</script>

<style lang="less" scoped>
.about {
  .right-icon {
    width: 24px;
    height: 24px;
  }

  .square-title {
    font-size: 16px;
    color: #016831;
  }

  /deep/ .van-nav-bar {
    background: #e8dddb;
  }

  .titleH{
    padding: 15px 0px 0px 0px;
  }

  .title-auto{
    width: 90px;
    height: 24px;
    margin: 0 auto;
  }

  .title-img{
    width: 24px;
  }

  .title-name{
    font-size: 16px;
    line-height: 24px;
    margin: 0 auto;
  }

}
</style>